.navbar {
  background: $navbarColor;
  @include flex;
  height: 10vh;
  padding: $gutterWidth / 2;
  align-items: center;
  .brand-logo {
    width: 100px;
    margin-right: auto; }
  .nav-link-container {
    display: none;
    .nav-link {
      text-transform: uppercase;
      color: #fff;
      text-decoration: none;
      @include letter-spacing;
      font-size: 1.25rem;
      padding: 1rem;
      margin: 0 1rem;
      vertical-align: text-top;
      @include transition; }
    .bottom-border {
      transform: perspective(1px) translateZ(0);
      box-shadow: 0 0 1px rgba(0, 0, 0, 0);
      position: relative;
      overflow: hidden;
      &::before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: #fff;
        height: 2px;
        @include transition; } }
    .bottom-border:hover:before {
      left: 0;
      right: 0; } } }

// Navbar mobile
.hamburger-menu {
  width: 2rem;
  cursor: pointer;
  display: inline-block;
  .bar {
    background: #fff;
    width: 100%;
    height: 5px;
    margin: 6px 0;
    @include transition; } }
.animated {
  .bar {
    &:first-child {
      transform: rotate(-45deg) translate(-9px, 6px); }
    &:nth-child(2) {
      opacity: 0; }
    &:last-child {
      transform: rotate(45deg) translate(-8px, -8px); } } }

.nav-mobile {
  @include flex;
  flex-direction: column;
  background: $navbarColor;
  text-align: center;
  justify-content: center;
  align-items: center;
  @include transition;
  .mobile-nav-link {
    width: 100%;
    text-decoration: none;
    color: #fff;
    font-size: 1.25rem;
    @include letter-spacing;
    text-transform: uppercase;
    padding: 1rem;
    border-bottom: 2px solid $secondaryColor;
    @include transition;
    &:hover {
      background: $secondaryColor; } } }

.display-none {
  display: none; }
