#media {
  background: $mainColor;
  @include flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between !important;
  text-align: center;
  #player {
    width: 300px;
    iframe {
      width: 100% !important; } }
  .video-wrapper {
    width: 50%;
    .youtube-video {
      width: 90%;
      height: 350px;
      border-color: $mainColor; } } }
