#bio {
  color: #fff;
  padding: (2 * $gutterWidth) 0;
  background: linear-gradient(115.90808106542045deg, rgba(0, 0, 0,1) 18.28515625%,rgba(130, 130, 130,1) 84.06640625%);
  .flex-wrapper {
    margin: auto;
    width: $mobileContainer;
    @include flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .bio-img {
      width: 250px;
      border-radius: 50%;
      padding: $gutterWidth; }
    .bio-text {
      text-align: center;
      @include letter-spacing;
      font-size: 1rem;
      line-height: 1.2rem;
      padding: $gutterWidth; } } }
